* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #f8f9fa;
  font-family: 'Ubuntu', sans-serif;
  font-family: 'Noto Sans KR', sans-serif;
}

header{
  background-color: #3b2479;
  width: 100%;
  height: 15vh;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.8),0px 0px 5px rgba(200,200,200,0.5); 
}

.logo{
  background-color: #3b2479;
  float: left;
  margin-left: 5vw;
  margin-top: 4vh;
  font-size: 2.5rem;
  color: white;
}

.login{
  background-color: #3b2479;
  width: 13vw;
  height: 9vh;
  background: white;
  border-radius: 1vh;
  border: 0;
  box-shadow: 0px 0px 6px rgba(0,0,0,0.8) ,0px 0px 3px rgba(200,200,200,0.5); 
}

.profile{
  background-color: #3b2479;
  float: right;
  position: relative;
  font-size: 1rem;
  width: 60px;
  height: 60px;
  background: white;
  border-radius: 70%;
  overflow: hidden;
  box-shadow: 0px 0px 15px rgba(0,0,0,0.8),0px 0px 5px rgba(200,200,200,0.5); 
}

.profileicon{
  position: absolute;
  width: 60px;
  object-fit: cover;
}

.beta{
  float: left;
  margin-left: 2vh;
  margin-top: 4.5vh;
  font-size: 1rem;
  background: #70c056;
  color: white;
  width: 3rem;
  border-radius: 0.5vh;
  text-align: center;
}

.dropdown{
  position: absolute;
  float: right;
  top: 10vh;
  width: 200px;
  height: 130px;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.8),0px 0px 5px rgba(200,200,200,0.5); 
  z-index: 5;
}

.loginProfile{
  position: relative;
  background: rgba(255, 255, 255, 0);
  float: right;
  margin-right: 5vw;
  margin-top: 2.8vh;
  font-size: 1rem;
  width: 13vw;
  height: 9vh;
}

.logout{
  width: 100%;
  height: 30%;
  border: 0;
  outline: 0;
  background: #3b2479;
  color: white;
}

.name{
  width: 100%;
  height: 25%;
  padding: 5px;
}

.token{
  width: 100%;
  height: 45%;
  border-top: 1px solid #3b2479;
  padding: 5px;
}


@media (max-width: 700px) {
  header{
    background-color: #3b2479;
    width: 100%;
    height: 12vh;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.8),0px 0px 5px rgba(200,200,200,0.5); 
  }

  .profile{
    background-color: #3b2479;
    float: right;
    position: relative;
    font-size: 1rem;
    width: 40px;
    height: 40px;
    background: white;
    border-radius: 70%;
    overflow: hidden;
    box-shadow: 0px 0px 15px rgba(0,0,0,0.8),0px 0px 5px rgba(200,200,200,0.5); 
  }

  .profileicon{
    position: absolute;
    width: 40px;
    object-fit: cover;
  }

  .dropdown{
    position: absolute;
    float: right;
    right: 3vw;
    top: 10vh;
    width: 200px;
    height: 130px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.8),0px 0px 5px rgba(200,200,200,0.5); 
    z-index: 5;
  }

  .logo{
    background-color: #3b2479;
    float: left;
    margin-left: 5vw;
    margin-top: 4vh;
    font-size: 1.7rem;
    color: white;
  }

  .beta{
    float: left;
    margin-left: 1.5vh;
    margin-top: 4.3vh;
    font-size: 0.8rem;
    background: #70c056;
    color: white;
    width: 2rem;
    border-radius: 0.5vh;
    text-align: center;
  }
}